.todo__item {
    display: flex;
    margin-bottom: 20px;
    justify-content: space-between;
    align-items: center;
    border: 1px solid #8280a2;
    border-radius: 8px;
    height: 60px;
    width: 100%;
}

.todo__item-label {
    display: flex;
    align-items: center;
    padding-left: 10px;
    cursor: pointer;
    font-size: 25px;
    font-weight: 400;
    width: 60%;
}

.todo__item-checkbox {
    margin-right: 10px;
    height: 20px;
    width: 25px;
}

.test-true {
    opacity: 0.3;
}

.todo__item-input {
    padding: 5px;
    border-radius: 8px;
    border: 2px solid #fecb3e;
    font-size: 22px;
    width: 100%;
    outline: none;
    cursor: pointer;
}

.todo__item-text {
    width: 100%;
    overflow-x: auto;
    margin-top: 5px;
    padding-bottom: 10px;
}

.todo__item-btn {
    width: 40px;
    height: 40px;
    border: 0;
    cursor: pointer;
    padding: 5px;
    margin-left: 5px;
    background: transparent;
}

.todo__icon {
    width: 100%;
    height: 100%;
}

.todo__icon-edit {
    fill: #424761;
}

.todo__icon-del {
    fill: #e55;
}

.color-true {
    opacity: 0.3;
}

@media (max-width: 576px) {
    .todo__item {
        height: 48px;
    }

    .todo__item-label {
        font-size: 20px;
    }

    .todo__item-input {
        font-size: 16px;
    }
}

@media (max-width: 425px) {
    .todo__item-label {
        display: flex;
        font-size: 16px;
    }

    .todo__item {
        height: 40px;
    }

    .todo__item-btn {
        width: 30px;
        height: 30px;
        margin-left: 0;
    }

    .todo__item-checkbox {
        height: 18px;
        width: 20px;
    }
}
